/*  Remedy
------------------------------------------------------------- */
* { box-sizing: border-box; background-repeat: no-repeat; }
body { margin: 0; }


/*  Colors 
------------------------------------------------------------- */
:root {
	--cream: #f6efe5;
	--faded-cream: #c0ae95;
	--orange: #f2502a;
	--light-grey: #bebdc2;
	--dark-grey: #323232;
	}


/*  Layout 
------------------------------------------------------------- */

body {
	background: var(--light-grey);
	font-family: Avenir, Roboto, Helvetica, sans-serif;
	font-size: 0.75rem;
	font-weight: 200;
	line-height: 1.25;
}
main {
	background: var(--cream);
	margin: 1rem;
	box-shadow: 0px -3px 6px rgba(0,0,0,0.15);
	display: grid;
	grid-template-columns: 75fr 215fr 315fr 85fr 75fr; 

		grid-template-columns: 75fr 215fr minmax(280px, 315fr) 85fr 75fr; 

	
	grid-template-rows: 75px 65px auto auto auto auto 75px;
	
		grid-template-rows: 75px minmax(65px, 1fr) max-content auto auto auto minmax(65px, 1fr) 75px;
		min-height: 97vh;
		min-width: 280px;
}
h1 {
	grid-column: 2 / 5;
	grid-row: 4 / 5;
	z-index: 2;
	color: var(--cream);
	font-size: 2.4rem;
	text-transform: uppercase;
	letter-spacing: 1.4vw;
	font-weight: 400;
	text-align: center;
	margin: 0.5rem 0.8rem 0.2rem 1.6rem;
}
@media (max-width: 800px) {
	h1 { 
		text-align: right;
	}
}
img {
	grid-column: 3 / 4;
	grid-row: 3 / 4;	
	z-index: 2;
	width: 100%;
	height: 100%;
}
p {
	grid-column: 3 / 4;
	grid-row: 5 / 6;	
	z-index: 2;	
	background: var(--orange);
	padding: 1rem;
	margin: 0;
}
p:first-line {
	font-weight: 600;
	letter-spacing: 1px;
}
.realtor {
	display: grid;
	grid-template-columns: 80px auto;
	margin-top: 0.25rem;
	grid-gap: 0.5em;
}
.realtor-logo {
	background-image: url(../img/logo.gif);
	width: 73px;
	height: 20px;
	text-indent: -1000000px;
}
.realtor-address {
	align-self: end;
}
footer {
	z-index: 2;
	grid-column: 3 / 4;
	grid-row: 6 / 7;	
	margin: 1rem 0 2rem;
	text-transform: lowercase;
	color: var(--faded-cream);
	display: grid;
	grid-gap: 0.48rem;
	grid-template-columns: max-content auto;
}
@media (max-width: 350px) {
	footer {
		margin-left: 1rem;
	}
}
.seagram {
	text-transform: uppercase;
	font-family: georgia;
	letter-spacing: 3px;
	font-weight: 600;
	color: var(--cream);
	margin-left: 0.5em;
}
.grey-background {
	grid-column: 2 / 5;
	grid-row: 2 / -2;		
	background-color: var(--dark-grey);
	background-image: url(../img/dark-grey-paper.gif);
	background-repeat: repeat;
	z-index: 1;
}
	
	